.map {
	height: 500px;
	background-color: white;
	padding: 1rem;
	border-radius: 20px;
	margin-top: 16px;
	box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
}

.map .leaflet-container {
	height: 100%;
}

.info-flag img {
	width: 100px;
	border-radius: 5px;
}

.info-flag {
	height: 80px;
	width: 100%;
	background-size: cover;
	border-radius: 8px;
	margin-bottom: 5px;
}

.info-name {
	font-size: 18px;
	font-weight: bold;
	color: #555;
}

.info-confirmed,
.info-recovered,
.info-deaths {
	font-size: 16px;
	margin-top: 5px;
}
